"use client";
import { useState, useEffect } from "react";
import Image from "next/image";
import Link from "next/link";

const ads = [
    { id: 1, img: "https://sharesport.news/upload/1738299572-MVkv.jpg", link: "#" },
    { id: 2, img: "https://sharesport.news/upload/1738299048-UFjf.jpg", link: "#" },
    { id: 3, img: "https://sharesport.news/upload/1737961282-Yjjad.jpg", link: "#" },
];

export default function BigBanner() {
    const [current, setCurrent] = useState(0);

    useEffect(() => {
        const timer = setInterval(() => {
            setCurrent((prev) => (prev + 1) % ads.length);
        }, 5000); // Change every 4 seconds
        return () => clearInterval(timer);
    }, []);

    return (
        <div className="relative w-full h-[200px] md:h-[400px] overflow-hidden rounded-xl shadow-lg my-6 group">
            {ads.map((ad, index) => (
                <Link
                    key={ad.id}
                    href={ad.link}
                    target="_blank"
                    className={`absolute inset-0 transition-opacity duration-1000 ease-in-out ${
                        index === current ? "opacity-100" : "opacity-0"
                    }`}
                >
                    <Image
                        src={ad.img}
                        alt="Advertisement"
                        fill
                        objectFit={'cover'}
                        className="object-cover"
                    />
                </Link>
            ))}

            {/* Dots Indicator */}
            <div className="absolute bottom-4 left-1/2 transform -translate-x-1/2 flex space-x-2">
                {ads.map((_, idx) => (
                    <button
                        key={idx}
                        onClick={() => setCurrent(idx)}
                        className={`w-3 h-3 rounded-full ${
                            current === idx ? "bg-white" : "bg-white/50"
                        }`}
                    />
                ))}
            </div>
        </div>
    );
}